import React from "react";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import Pure from "./Pure";
import { BuildingsMap, BuildingsName, BuildingsType } from "./CLASSES/buildings";
import { TroopsMap, RaceName, RaceTroops, TroopRace, TroopsName } from "./CLASSES/troops";
import { mDialog } from "./SubComponents/Modal";
import "./Guid.css";

const Updater = {
    ui: (type, display) => { }
}

function GuidRouter(type, display) {
    return (e => {
        e.preventDefault();
        Updater.ui(type, Number(display));
    })
}

class BuildingInfo extends Pure {
    gid = this.props.gid || "1";
    render() {
        let building = new BuildingsMap[this.gid];
        return <>
            <b>{building.name}</b>
        </>;
    }
}

class BuildingsTypesInfo extends Pure {
    type = this.props.type || "1";
    title = ["الموارد", "الجيش", "البنية التحتية"];
    render() {
        let Buildings = BuildingsType[this.type];
        let Content = [];
        Buildings.forEach((gid, index) => {
            Content.push(<li key={index}><Link to="#" onClick={GuidRouter(4, gid)}>{BuildingsName[gid]}</Link></li>)
        });
        return <ul>
            <li><b>المباني ({this.title[Number(this.type) - 1]})</b></li>
            <ul>{Content}</ul>
        </ul>
    }
}

class TroopInfo extends Pure {
    unit = this.props.unit || "1";
    render() {
        let troop = new TroopsMap[this.unit];
        return <>
            <b>{troop.name}</b>
        </>
    }
}

class RaceInfo extends Pure {
    race = this.props.race || "1";
    render() {
        /** @type {Number[]} */
        let Troops = RaceTroops[this.race];
        let Content = [];
        Troops.forEach((unit, index) => {
            Content.push(<li key={index}><Link to="#" onClick={GuidRouter(2, unit)}>{TroopsName[unit]}</Link></li>);
        });
        return <ul>
            <li><b>قبيلة {RaceName[this.race]}</b></li>
            <ul>{Content}</ul>
        </ul>;
    }
}

class Main extends Pure {
    Race() {
        let race = [];
        for (const R in RaceName) {
            race.push(<li key={R}><Link to="#" onClick={GuidRouter(1, R)}>{RaceName[R]}</Link></li>)
        }
        return race;
    }
    render() {
        return <>
            <ul>
                <li>القبائل:</li>
                <ul>{this.Race()}</ul>
            </ul>
            <br />
            <ul>
                <li>المباني:</li>
                <ul>
                    <li><Link to="#" onClick={GuidRouter(3, 1)}>الموارد</Link></li>
                    <li><Link to="#" onClick={GuidRouter(3, 2)}>الجيش</Link></li>
                    <li><Link to="#" onClick={GuidRouter(3, 3)}>البنية التحتية</Link></li>
                </ul>
            </ul>
        </>;
    }
}

export default class Guid extends Pure {
    state = { update: 0 }
    /** @type {Number[][]} */
    stack = [];
    constructor(props) {
        super(props);
        this.type = this.props.type || 0;
        this.display = this.props.display || 0;
    }
    onReady() {
        Updater.ui = (type, display) => {
            this.stack.push([this.type, this.display]);
            this.type = type;
            this.display = display;
            this.refresh();
        }
    }
    onRemoved() {
        Updater.ui = (type, display) => { };
    }
    refresh() {
        this.updateState({ update: Date.now() });
    }
    navigate() {
        if (this.stack.length === 0) {
            mDialog.hide();
        } else {
            let info = this.stack.pop();
            this.type = info[0];
            this.display = info[1];
            this.refresh();
        }
    }
    Data() {
        if (this.type === 0)
            return <Main />;
        else if (this.type === 1)
            return <RaceInfo race={this.display} />;
        else if (this.type === 2)
            return <TroopInfo unit={this.display} />;
        else if (this.type === 3)
            return <BuildingsTypesInfo type={this.display} />;
        else if (this.type === 4)
            return <BuildingInfo gid={this.display} />;
    }
    render() {
        return <>
            <div className="user-guid">{this.Data()}</div>
            <div className="guid-footer">
                <Button variant="secondary" onClick={() => this.navigate()}>{this.stack.length > 0 ? "عودة" : "إغلاق"}</Button>
            </div>
        </>;
    }
}