class GenericBase {
    name = "";
    description = "";
    image = "";
    villageId = 0;
    buildingId = 0;
    placeId = 0;
    level = 0;
    /** @type {Number[][]} */
    costs = [];
    /** @type {Number[]} */
    culturePoints = [];
    /** @type {Number[]} */
    consumption = [];
    /** @type {Number[][]} */
    preRequiresites = [];
    /** @type {Number[]} */
    time = [];
    capital = 0;
    race = [true, true, true, true, true];
    title() {
        return `${this.name} المستوى ${this.level}`;
    }
    get maxLevel() {
        return this.consumption.length;
    }
}

