import React from "react";
import Pure from "../Pure";
import { Modal } from "react-bootstrap";
import { Button } from "react-bootstrap";
import "./Modal.css"

const mDialog = {
    show: () => { },
    hide: () => { }
}

class DialogBox extends Pure {
    state = { visiable: this.props.visiable || false }
    footer = this.props.footer || false;
    onReady() {
        mDialog.show = () => this.show();
        mDialog.hide = () => this.hide();
    }
    onRemoved() {
        mDialog.show = () => { };
        mDialog.hide = () => { };
    }
    show() {
        this.updateState({ visiable: true });
    }
    hide() {
        this.updateState({ visiable: false });
    }
    render() {
        return <Modal show={this.state.visiable} onHide={() => this.hide()}>
            <Modal.Header closeButton>
                <Modal.Title>{this.props.title}</Modal.Title>
            </Modal.Header>
            <Modal.Body>{this.props.children}</Modal.Body>
            {
                this.footer ?
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => this.hide()}>إغلاق</Button>
                    </Modal.Footer>
                    :
                    <></>
            }
        </Modal>;
    }
}

export { DialogBox, mDialog }